const GradientBackground = () => {
  const { docsConfig } = useContext(DocsConfigContext);
  const colors = useColors();

  if (docsConfig?.background == null) {
    return null;
  }

  const gradientLight = getGradientBackground(docsConfig.background, colors.primary, 'light');
  const gradientDark = getGradientBackground(docsConfig.background, colors.primaryLight, 'dark');

  if (gradientLight == null || gradientDark == null) {
    return null;
  }

  return (
    <>
      <span
        className={cn(
          'block absolute dark:hidden inset-0 overflow-hidden',
          gradientLight.className
        )}
        style={{
          background: gradientLight.background,
        }}
      >
        {gradientLight.children}
      </span>
      <span
        className={cn('hidden absolute dark:block inset-0 overflow-hidden', gradientDark.className)}
        style={{
          background: gradientDark.background,
        }}
      >
        {gradientDark.children}
      </span>
    </>
  );
};

const BackgroundImage = ({
  backgroundImage,
  className,
}: {
  backgroundImage: string | undefined;
  className?: string;
}) => (
  <span
    className={cn('fixed inset-0', className)}
    style={{
      backgroundImage: `url('${backgroundImage}')`,
      backgroundRepeat: 'no-repeat',
      backgroundPosition: 'top right',
      backgroundAttachment: 'fixed',
      WebkitTouchCallout: 'none',
    }}
  />
);

export const Background = () => {
  const { docsConfig } = useContext(DocsConfigContext);
  const { pageMetadata } = useContext(PageContext);
  const theme = docsConfig?.theme;
  const isCustom = pageMetadata.mode === 'custom';

  return (
    <>
      <span
        className={cn(
          'fixed inset-0 bg-background-light dark:bg-background-dark -z-10',
          !isCustom && theme === 'almond' && 'bg-[#F4F4F5] dark:bg-[#09090B]'
        )}
      />
      <GradientBackground />
      {typeof docsConfig?.background?.image === 'string' && (
        <BackgroundImage backgroundImage={docsConfig.background.image} />
      )}
      {typeof docsConfig?.background?.image === 'object' && (
        <>
          <BackgroundImage
            backgroundImage={docsConfig.background.image.light}
            className="dark:hidden"
          />
          <BackgroundImage
            backgroundImage={docsConfig.background.image.dark}
            className="hidden dark:block"
          />
        </>
      )}
    </>
  );
};
