import { StaticPropsOrgType } from '@mintlify/models';
import posthog from 'posthog-js';

import { IS_DEV, TWO_WEEKS_IN_MILLISECONDS } from '@/constants';

export const isOrgLessThanTwoWeeksOld = (
  createdAt: StaticPropsOrgType['createdAt'] | undefined
): boolean => {
  if (!createdAt) {
    return false;
  }
  return Date.now() - new Date(createdAt).getTime() < TWO_WEEKS_IN_MILLISECONDS;
};

export const initPosthogSession = async (org?: StaticPropsOrgType) => {
  if (IS_DEV || !org || posthog.sessionRecordingStarted()) {
    return;
  }

  const { plan, createdAt } = org;
  const isPaid = plan === 'startup' || plan === 'enterprise';

  if (isPaid) {
    posthog.startSessionRecording();
  }

  // Record is the deployment is on a 2 week trial
  if (isOrgLessThanTwoWeeksOld(createdAt)) {
    posthog.startSessionRecording();
  }
};
