'use client';

import { PirschConfig } from '@mintlify/models';
import type { PirschWebClient } from 'pirsch-sdk/web';

import { AnalyticsService } from '@/analytics/AnalyticsService';

export default class PirschAnalytics extends AnalyticsService<PirschConfig> {
  initialized = false;

  // Store events to submit after the library is loaded.
  eventQueue: { eventName: string; eventProperties: Record<string, string | number> }[] = [];
  pirsch: PirschWebClient | undefined;

  init(implementationConfig: PirschConfig) {
    if (!implementationConfig.id) {
      return;
    }
    const pirschId = implementationConfig.id;
    import('pirsch-sdk/web')
      .then((_pirsch) => {
        if (!this.initialized) {
          // Get default module export
          this.pirsch = new _pirsch.Pirsch({
            identificationCode: pirschId,
          });

          this.initialized = true;
          this.eventQueue.forEach(({ eventName, eventProperties }) => {
            if (this.pirsch) {
              void this.pirsch.event(eventName, undefined, eventProperties);
            }
          });
        }
      })
      .catch((e) => {
        console.error(e);
      });
  }

  createEventListener(eventName: string) {
    const captureFunc = async function capture(this: PirschAnalytics, eventProperties: object) {
      const typedEventProperties = eventProperties as Record<string, string | number>;
      if (this.pirsch) {
        void this.pirsch.event(eventName, undefined, typedEventProperties);
      } else {
        this.eventQueue.push({ eventName, eventProperties: typedEventProperties });
      }
    };
    return captureFunc.bind(this);
  }

  onRouteChange(_: string): void {
    if (this.pirsch) {
      void this.pirsch.hit();
    }
  }
}
