import Script from 'next/script';

export default function FrontChatScript({ frontchat }: { frontchat?: { snippetId?: string } }) {
  if (!frontchat?.snippetId) {
    return null;
  }

  return (
    <>
      {/* eslint-disable-next-line @next/next/no-before-interactive-script-outside-document */}
      <Script
        strategy="beforeInteractive"
        src="https://chat-assets.frontapp.com/v1/chat.bundle.js"
      />
      <Script
        id="frontchat"
        strategy="afterInteractive"
      >{`window.FrontChat('init', {chatId: '${frontchat.snippetId}', useDefaultLauncher: true});`}</Script>
    </>
  );
}
