'use client';

import { useEffect } from 'react';
import type { Socket } from 'socket.io-client';

import { useMintlifyRouter } from '@/contexts/MintlifyRouterContext';

const useHotReload = () => {
  const router = useMintlifyRouter();
  useEffect(() => {
    let socket: Socket | undefined;
    let terminated = false;
    const connect = async () => {
      const { default: io } = await import('socket.io-client');

      // if the component is unmounted before socket.io is loaded, don't connect
      if (terminated) return;

      socket = io();

      socket.on('reload', () => {
        router.reload();
      });
    };
    void connect();

    return () => {
      terminated = true;
      socket?.disconnect();
    };
  }, [router]);
};

export const HotReloader = () => {
  useHotReload();

  return <></>;
};
