'use client';

/* eslint-disable @next/next/no-before-interactive-script-outside-document */
import Script from 'next/script';
import { useContext } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';

export const ModeToggleScript = () => {
  const { docsConfig } = useContext(DocsConfigContext);

  const defaultMode = docsConfig?.appearance?.default;

  return (
    <Script
      id="mode-toggle"
      strategy="beforeInteractive"
      dangerouslySetInnerHTML={{
        __html: `
      try {
        if (localStorage.isDarkMode === 'true') {
          document.documentElement.classList.add('dark');
        } else if (localStorage.isDarkMode === 'false') {
          document.documentElement.classList.remove('dark');
        } else if ((${(
          defaultMode == null
        ).toString()} && !('isDarkMode' in localStorage) && window.matchMedia('(prefers-color-scheme: dark)').matches) || ${(
          defaultMode === 'dark'
        ).toString()}) {
          document.documentElement.classList.add('dark');
        } else {
          document.documentElement.classList.remove('dark');
        }
      } catch (_) {}`,
      }}
    />
  );
};
