import Script from 'next/script';

export default function OsanoScript({ osano }: { osano?: { scriptSource?: string } }) {
  if (!osano?.scriptSource) {
    return null;
  }

  if (
    !osano.scriptSource.startsWith('https://cmp.osano.com/') ||
    !osano.scriptSource.endsWith('/osano.js')
  ) {
    return null;
  }

  /* eslint-disable-next-line @next/next/no-before-interactive-script-outside-document */
  return <Script strategy="beforeInteractive" id="osano" src={osano.scriptSource} />;
}
