'use client';

import { FathomConfig } from '@mintlify/models';
import { PageViewOptions } from 'fathom-client';

import { AnalyticsService, RouteProps } from '@/analytics/AnalyticsService';

export default class FathomAnalytics extends AnalyticsService<FathomConfig> {
  initialized = false;
  trackPageview: ((opts?: PageViewOptions) => void) | undefined;

  init(implementationConfig: FathomConfig) {
    if (!implementationConfig.siteId) {
      return;
    }

    const { siteId } = implementationConfig;

    import('fathom-client')
      .then((_fathom) => {
        if (!this.initialized) {
          _fathom.load(siteId);

          // The Fathom library uses asterisk imports (ie. * as Fathom)
          // so there is no default export for us to store a reference to.
          // Instead, we keep a reference to the method we need.
          this.trackPageview = _fathom.trackPageview;

          this.initialized = true;
        }
      })
      .catch((e) => {
        console.error(e);
      });
  }

  createEventListener(_: string): (eventProperties: object) => Promise<void> {
    return () => Promise.resolve();
  }

  onRouteChange(_: string, routeProps: RouteProps) {
    if (this.trackPageview && !routeProps.shallow) {
      this.trackPageview();
    }
  }
}
