import type { DecoratedNavigationPage, PageMetaTags } from '@mintlify/models';
import {
  DecoratedGroupConfig,
  DecoratedNavigationConfig,
  Division,
  divisions,
  DocsConfig,
  NavigationConfig,
} from '@mintlify/validation';

import { NEXT_PUBLIC } from '../env';
import { getTitle } from './getAllMetaTags';
import { isNavigationGroup } from './nav/isNavigation';
import { getSectionTitle } from './paths/getSectionTitle';
import { isEqualIgnoringLeadingSlash } from './paths/isEqualIgnoringLeadingSlash';

const divisionNameMap: Record<Division, string> = {
  anchors: 'anchor',
  dropdowns: 'dropdown',
  languages: 'language',
  tabs: 'tab',
  versions: 'version',
};

const findActiveDivision = (
  path: string,
  nav: DecoratedNavigationConfig | undefined,
  parent: string = 'Documentation'
): string => {
  if (!nav) return parent;
  if ('groups' in nav) {
    for (const group of nav.groups as DecoratedGroupConfig[]) {
      const found = findPage(path, group);
      if (found) {
        return parent;
      }
    }
  }

  for (const division of divisions) {
    if (division in nav) {
      const items = nav[division as keyof typeof nav] as DecoratedNavigationConfig[];
      for (const item of items) {
        findActiveDivision(
          path,
          item,
          item[divisionNameMap[division] as keyof typeof item] as string
        );
      }
    }
  }

  return parent;
};

function findPage(
  path: string,
  nav: DecoratedNavigationConfig
): DecoratedNavigationPage | undefined {
  if ('pages' in nav) {
    for (const page of nav.pages) {
      if (isNavigationGroup(page)) {
        const found = findPage(path, page as DecoratedGroupConfig);
        if (found) {
          return found;
        }
      } else if (isEqualIgnoringLeadingSlash(page.href, path)) {
        return page;
      }
    }
  }
}

export function findActiveGroupFromNav(currentPath: string, nav?: DecoratedNavigationConfig) {
  if (!nav) return undefined;

  if ('groups' in nav) {
    for (const group of nav.groups as DecoratedGroupConfig[]) {
      const title = findSectionTitleRecursively(group, currentPath);
      if (title) {
        return title;
      }
    }
  }

  for (const key of divisions) {
    if (key in nav) {
      const items = nav[key as keyof typeof nav] as NavigationConfig[];
      for (const item of items) {
        const title = getSectionTitle(currentPath, item);
        if (title) {
          return title;
        }
      }
    }
  }
}

function findSectionTitleRecursively(
  navGroup: DecoratedGroupConfig,
  currentPath: string
): string | undefined {
  if ('pages' in navGroup) {
    for (const entry of navGroup.pages) {
      if (isNavigationGroup(entry)) {
        const title = findSectionTitleRecursively(entry as DecoratedGroupConfig, currentPath);
        if (title) {
          return title;
        }
      } else if (isEqualIgnoringLeadingSlash(entry.href, currentPath)) {
        return navGroup.group;
      }
    }
  }
}

export function getOGImageEndpoint(
  subdomain: string | undefined,
  pageMetadata: PageMetaTags,
  docsConfig: DocsConfig,
  path: string,
  docsNavWithMetadata?: DecoratedNavigationConfig
): string {
  const params = new URLSearchParams();
  const titleParam = getTitle(pageMetadata);

  const setQueryParamIfExists = (param: string, value: string | undefined | void) => {
    if (value) {
      params.set(param, value);
    }
  };

  const division = findActiveDivision(path, docsNavWithMetadata);
  const group = findActiveGroupFromNav(path, docsNavWithMetadata);

  setQueryParamIfExists('division', division);
  setQueryParamIfExists('section', group);

  setQueryParamIfExists('mode', docsConfig.appearance?.default);

  setQueryParamIfExists('title', titleParam);
  setQueryParamIfExists('description', pageMetadata.description);

  if (typeof docsConfig.logo === 'string') {
    setQueryParamIfExists('logoLight', docsConfig.logo);
    setQueryParamIfExists('logoDark', docsConfig.logo);
  } else if (typeof docsConfig.logo === 'object') {
    setQueryParamIfExists('logoLight', docsConfig.logo.light);
    setQueryParamIfExists('logoDark', docsConfig.logo.dark);
  }

  setQueryParamIfExists('primaryColor', docsConfig.colors.primary);
  setQueryParamIfExists('lightColor', docsConfig.colors.light);
  setQueryParamIfExists('darkColor', docsConfig.colors.dark);

  const encodedPath = encodeURIComponent(`/api/og?${params.toString()}`);
  // Fallback to Mintlify's hosted endpoint for search engines that don't support JavaScript.
  const optimizedOgImageUrl = `https://${subdomain || 'mintlify'}.mintlify.app${NEXT_PUBLIC.ASSET_PREFIX}/_next/image?url=${encodedPath}&w=1200&q=100`;

  return optimizedOgImageUrl;
}
