import { useContext } from 'react';

import { IS_PROD } from '@/constants';
import { DocsConfigContext } from '@/contexts/ConfigContext';

import FrontChatScript from './FrontChatScript';
import OsanoScript from './OsanoScript';

const IntegrationsScripts = ({ cookiesEnabled }: { cookiesEnabled?: boolean }) => {
  const { docsConfig } = useContext(DocsConfigContext);

  if (!IS_PROD || !cookiesEnabled) return null;
  function PrioritizedScripts() {
    return (
      <>
        <OsanoScript osano={docsConfig?.integrations?.osano} />
      </>
    );
  }

  return (
    <>
      <PrioritizedScripts />
      <FrontChatScript frontchat={docsConfig?.integrations?.frontchat} />
    </>
  );
};

export default IntegrationsScripts;
