'use client';

import { useContext } from 'react';

import ClearbitScript from '@/analytics/scripts/ClearbitScript';
import GA4Script from '@/analytics/scripts/GA4Script';
import GTMScript from '@/analytics/scripts/GTMScript';
import HeapScript from '@/analytics/scripts/HeapScript';
import KoalaScript from '@/analytics/scripts/KoalaScript';
import PlausibleScript from '@/analytics/scripts/PlausibleScript';
import { IS_PROD } from '@/constants';
import { DocsConfigContext } from '@/contexts/ConfigContext';

import SegmentScript from './SegmentScript';

const AnalyticsScripts = ({ cookiesEnabled }: { cookiesEnabled?: boolean }) => {
  const { docsConfig } = useContext(DocsConfigContext);

  if (!IS_PROD || !cookiesEnabled) return null;
  return (
    <>
      <ClearbitScript clearbit={docsConfig?.integrations?.clearbit} />
      <GA4Script ga4={docsConfig?.integrations?.ga4} />
      <GTMScript gtm={docsConfig?.integrations?.gtm} />
      <HeapScript heap={docsConfig?.integrations?.heap} />
      <KoalaScript koala={docsConfig?.integrations?.koala} />
      <PlausibleScript plausible={docsConfig?.integrations?.plausible} />
      <SegmentScript segment={docsConfig?.integrations?.segment} />
    </>
  );
};

export default AnalyticsScripts;
