'use client';

import { AnalyticsMediatorInterface } from './AnalyticsMediator';

export type RouteProps = {
  initialLoad?: boolean;
  shallow?: boolean;
};

export abstract class AnalyticsService<T = unknown> implements AnalyticsMediatorInterface {
  abstract init(implementationConfig: T): void;
  abstract createEventListener(eventName: string): (eventProperties: object) => Promise<void>;
  abstract onRouteChange(url: string, routeProps: RouteProps): void;
}
