'use client';

import { ClearbitConfig } from '@mintlify/models';
import Script from 'next/script';

export default function ClearbitScript({ clearbit }: { clearbit?: ClearbitConfig }) {
  if (!clearbit?.publicApiKey) {
    return null;
  }

  return (
    <>
      <Script
        strategy="afterInteractive"
        src={`https://tag.clearbitscripts.com/v1/${clearbit.publicApiKey}/tags.js`}
      />
    </>
  );
}
