'use client';

import { DocsConfig } from '@mintlify/validation';
import { useRouter } from 'next/router';
import Script from 'next/script';
import { useEffect, useState } from 'react';
import * as React from 'react';

import AnalyticsContext from '@/analytics/AnalyticsContext';
import AnalyticsScripts from '@/analytics/scripts/AnalyticsScripts';
import { useAnalytics } from '@/analytics/useAnalytics';
import { HotReloader } from '@/components/HotReloader';
import { BannerProvider } from '@/contexts/BannerContext';
import {
  DocsConfigContext,
  ApiReferenceContext,
  AuthContext,
  DeploymentMetadataContext,
  PageContext,
} from '@/contexts/ConfigContext';
import { LocalStorageContextController } from '@/contexts/LocalStorageContext';
import { MintlifyRouterContext } from '@/contexts/MintlifyRouterContext';
import { NavigationContextController } from '@/contexts/NavigationContext';
import { NEXT_PUBLIC } from '@/env';
import FontScript from '@/fonts/FontScript';
import { useCookiesEnabled } from '@/hooks/useCookiesEnabled';
import useProgressBar from '@/hooks/useProgressBar';
import { useUserInfo } from '@/hooks/useUserInfo';
import Intercom from '@/integrations/Intercom';
import IntegrationsScripts from '@/integrations/scripts/IntegrationsScripts';
import { ThemeLayout } from '@/themes';
import { PageProps } from '@/types/page';
import { CanonicalTags } from '@/ui/CanonicalTags';
import { ColorVariables } from '@/ui/ColorVariables';
import { Head } from '@/ui/Head';
import { ModeToggleScript } from '@/ui/ModeToggleScript';
import { SearchProvider } from '@/ui/search';
import { checkAccess } from '@/utils/entitlements';
import { getAllMetaTags } from '@/utils/getAllMetaTags';
import { getLegacyThemeSettings } from '@/utils/getLegacyThemeSettings';
import { getOGImageEndpoint } from '@/utils/getOGImageEndpoint';
import { initPosthogSession, isOrgLessThanTwoWeeksOld } from '@/utils/initPosthogSession';

import packageJson from '../../package.json';
import { Background } from './Background';
import ErrorBoundary from './ErrorBoundary';
import { Fonts } from './Fonts';
import { MdxContent } from './MdxContent';
import { RoundedVariables } from './RoundedVariables';

// entry point for [[..slug]]
export default function Page({
  cssFiles,
  description,
  disableAiChat,
  entitlements,
  favicons,
  feedback,
  inkeep,
  internalAnalyticsWriteKey,
  jsFiles,
  mdxExtracts,
  mdxSource,
  mdxSourceWithNoJs,
  org,
  pageData,
  shouldIndex,
  subdomain,
  // TODO: Deprecate this in favor of subdomain
  actualSubdomain,
  trieve,
  userAuth,
  auth,
  gitSource,
  banner,
  buildId,
  panelMdxSource,
  panelMdxSourceWithNoJs,
}: PageProps) {
  const router = useRouter();
  const { mintConfig, pageMetadata, apiReferenceData, docsConfig, docsNavWithMetadata } = pageData;
  const telemetryDisabled = docsConfig.integrations?.telemetry?.enabled === false;
  const cookiesEnabled = useCookiesEnabled(docsConfig);
  const integrationsConfig = docsConfig.integrations ?? {};
  const analyticsMediator = useAnalytics(
    integrationsConfig,
    subdomain,
    internalAnalyticsWriteKey,
    telemetryDisabled,
    cookiesEnabled
  );
  const [origin, setOrigin] = useState('');

  const { userInfo, isFetchingUserInfo } = useUserInfo(userAuth, router);

  const isCli = NEXT_PUBLIC.ENV === 'cli';

  const clientVersion = packageJson.version;

  // Optionally override the theme for development purposes
  docsConfig.theme = (process.env.THEME as DocsConfig['theme'] | undefined) ?? docsConfig.theme;

  // Optionally override custom JS for testing purposes
  const customJSdisabled = NEXT_PUBLIC.CUSTOM_JS_DISABLED === 'true';

  useProgressBar(docsConfig.colors.primary);

  if (!telemetryDisabled && cookiesEnabled) {
    void initPosthogSession(org);
  }

  const hasChatPermissions =
    (checkAccess(entitlements, 'AI_CHAT') || isOrgLessThanTwoWeeksOld(org?.createdAt)) &&
    !disableAiChat;

  useEffect(() => {
    setOrigin(window.location.origin);
  }, []);

  const metaTagsDict = getAllMetaTags(pageMetadata, docsConfig, actualSubdomain);

  const ogImageUrl = getOGImageEndpoint(
    actualSubdomain,
    pageMetadata,
    docsConfig,
    router.asPath,
    docsNavWithMetadata
  );

  return (
    <MintlifyRouterContext.Provider value={router}>
      {isCli && <HotReloader />}
      <Intercom appId={docsConfig.integrations?.intercom?.appId} autoBoot>
        <AuthContext.Provider value={{ userAuth, userInfo, auth, isFetchingUserInfo }}>
          <DeploymentMetadataContext.Provider
            value={{
              subdomain,
              actualSubdomain,
              gitSource,
              inkeep,
              trieve,
              feedback,
              entitlements,
              buildId,
              clientVersion,
            }}
          >
            <ApiReferenceContext.Provider value={{ apiReferenceData }}>
              <DocsConfigContext.Provider
                value={{
                  mintConfig,
                  docsConfig,
                  docsNavWithMetadata,
                  legacyThemeSettings: getLegacyThemeSettings(docsConfig, mintConfig),
                }}
              >
                <PageContext.Provider
                  value={{
                    pageMetadata,
                    mdxExtracts,
                    description,
                    panelMdxSource,
                    panelMdxSourceWithNoJs,
                  }}
                >
                  <NavigationContextController>
                    <FontScript />
                    <AnalyticsContext.Provider value={{ analyticsMediator }}>
                      <ColorVariables />
                      <RoundedVariables />
                      <Head
                        favicons={favicons}
                        metaTagsDict={metaTagsDict}
                        ogImageUrl={ogImageUrl}
                      />
                      <CanonicalTags
                        shouldIndex={shouldIndex}
                        origin={origin}
                        basePath={router.basePath}
                      />
                      <ModeToggleScript />
                      {cssFiles.map(({ content }, i) => (
                        <style
                          key={i}
                          dangerouslySetInnerHTML={{
                            __html: content,
                          }}
                        />
                      ))}
                      {!customJSdisabled &&
                        jsFiles.map(({ content }, i) => (
                          <Script id={i.toString()} key={i}>
                            {content}
                          </Script>
                        ))}
                      <IntegrationsScripts cookiesEnabled={cookiesEnabled} />
                      <AnalyticsScripts cookiesEnabled={cookiesEnabled} />
                      <SearchProvider subdomain={subdomain} hasChatPermissions={hasChatPermissions}>
                        <Background />
                        <Fonts>
                          <LocalStorageContextController cookiesEnabled={cookiesEnabled}>
                            <BannerProvider
                              initialBanner={banner ?? null}
                              config={docsConfig.banner}
                              subdomain={subdomain}
                            >
                              <ThemeLayout>
                                <ErrorBoundary>
                                  <MdxContent
                                    mdxSource={mdxSource}
                                    mdxSourceWithNoJs={mdxSourceWithNoJs}
                                    pageMetadata={pageMetadata}
                                    userInfo={userInfo}
                                  />
                                </ErrorBoundary>
                              </ThemeLayout>
                            </BannerProvider>
                          </LocalStorageContextController>
                        </Fonts>
                      </SearchProvider>
                    </AnalyticsContext.Provider>
                  </NavigationContextController>
                </PageContext.Provider>
              </DocsConfigContext.Provider>
            </ApiReferenceContext.Provider>
          </DeploymentMetadataContext.Provider>
        </AuthContext.Provider>
      </Intercom>
    </MintlifyRouterContext.Provider>
  );
}
