'use client';

import { DocsConfig } from '@mintlify/validation';
import { useEffect, useState } from 'react';
import { useLocalStorage } from 'usehooks-ts';

export const useCookiesEnabled = (docsConfig?: DocsConfig) => {
  const cookieConsentRequired =
    docsConfig == undefined ? true : docsConfig.integrations?.cookies !== undefined;
  const cookieConsentKey = docsConfig?.integrations?.cookies?.key ?? '';
  const cookieConsentValue = docsConfig?.integrations?.cookies?.value ?? '';
  const initialCookieConsent = cookieConsentRequired ? false : true;
  const [consent, setConsent] = useState(initialCookieConsent);
  const [storedValue, _setStoredValue] = useLocalStorage(cookieConsentKey, '');

  useEffect(() => {
    if (cookieConsentRequired && cookieConsentKey !== '' && cookieConsentValue !== '') {
      if (storedValue.toString() === cookieConsentValue.toString()) {
        setConsent(true);
      } else if (storedValue.toString() !== cookieConsentValue.toString()) {
        setConsent(false);
      }
    }
  }, [storedValue, cookieConsentRequired, cookieConsentKey, cookieConsentValue]);

  return consent;
};
