'use client';

import { PlausibleConfig } from '@mintlify/models';
import Script from 'next/script';

export default function PlausibleScript({ plausible }: { plausible?: PlausibleConfig }) {
  if (!plausible?.domain) {
    return null;
  }

  return (
    <Script
      strategy="afterInteractive"
      data-domain={plausible.domain}
      src={`https://${plausible.server ?? 'plausible.io'}/js/script.js`}
    />
  );
}
