'use client';

// IMPROVEMENT OPPORTUNITY: Figure out how to dynamically import
import { LogrocketConfig } from '@mintlify/models';
import LogRocket from 'logrocket';

import { AnalyticsService, RouteProps } from '@/analytics/AnalyticsService';

export default class LogrocketAnalytics extends AnalyticsService<LogrocketConfig> {
  initialized = false;
  trackEvent: typeof LogRocket.track | undefined;

  init(implementationConfig: LogrocketConfig) {
    if (!implementationConfig.appId) {
      return;
    }

    try {
      if (!this.initialized && implementationConfig.appId) {
        LogRocket.init(implementationConfig.appId);
        this.trackEvent = LogRocket.track;
        this.initialized = true;
      }
    } catch (e) {
      console.error(e);
    }
  }

  createEventListener(eventName: string) {
    if (this.initialized) {
      const trackEvent = this.trackEvent;
      return async function capture(eventProperties: object) {
        if (trackEvent) trackEvent(eventName, eventProperties as Record<string, string>);
      };
    }
    return async function doNothing(_: object) {
      return;
    };
  }
  onRouteChange(_url: string, _routeProps: RouteProps): void {
    return;
  }
}
