'use client';

import { HotjarConfig } from '@mintlify/models';

import { AnalyticsService, RouteProps } from '@/analytics/AnalyticsService';

export default class HotjarAnalytics extends AnalyticsService<HotjarConfig> {
  initialized = false;
  // Store events to submit after the library is loaded.
  eventQueue: string[] = [];
  hotjar:
    | {
        initialize: (hjid: number, hjsv: number) => void;
        event: (name: string) => void;
      }
    | undefined;

  init(implementationConfig: HotjarConfig) {
    if (!implementationConfig.hjid || !implementationConfig.hjsv) {
      return;
    }
    const hjid = parseInt(implementationConfig.hjid, 10);
    const hjsv = parseInt(implementationConfig.hjsv, 10);

    import('react-hotjar')
      .then((_hotjar) => {
        if (!this.initialized) {
          // Get default module export
          this.hotjar = _hotjar.hotjar;
          this.hotjar.initialize(hjid, hjsv);
          this.initialized = true;

          this.eventQueue.forEach((eventName) => {
            if (this.hotjar) {
              this.hotjar.event(eventName);
            }
          });
        }
      })
      .catch((e) => {
        console.error(e);
      });
  }

  createEventListener(eventName: string) {
    const captureFunc = async function capture(this: HotjarAnalytics, _: object) {
      if (this.hotjar) {
        this.hotjar.event(eventName);
      } else {
        this.eventQueue.push(eventName);
      }
    };
    return captureFunc.bind(this);
  }
  onRouteChange(_url: string, _routeProps: RouteProps): void {
    return;
  }
}
