'use client';

import { Types } from '@amplitude/analytics-browser';
import { AmplitudeConfig } from '@mintlify/models';

import { AnalyticsService, RouteProps } from '@/analytics/AnalyticsService';

export default class AmplitudeAnalytics extends AnalyticsService<AmplitudeConfig> {
  initialized = false;
  track:
    | ((
        eventInput: string | Types.BaseEvent,
        eventProperties?: Record<string, unknown> | undefined,
        eventOptions?: Types.EventOptions | undefined
      ) => Types.AmplitudeReturn<Types.Result>)
    | undefined;

  init(implementationConfig: AmplitudeConfig) {
    if (!implementationConfig.apiKey) {
      return;
    }

    const { apiKey } = implementationConfig;

    import('@amplitude/analytics-browser')
      .then((_amplitude) => {
        if (!this.initialized) {
          _amplitude.init(apiKey);
          this.track = _amplitude.track;
          this.initialized = true;
        }
      })
      .catch((e) => {
        this.track = undefined;
        console.error(e);
      });
  }

  createEventListener(eventName: string) {
    if (this.initialized && this.track) {
      const func = async function capture(this: AmplitudeAnalytics, eventProperties: object) {
        if (this.track) {
          this.track(eventName, eventProperties as Record<string, unknown>);
        }
      };
      return func.bind(this);
    }
    return async function doNothing(_: object) {
      return;
    };
  }

  onRouteChange(url: string, routeProps: RouteProps) {
    if (this.initialized && this.track && !routeProps.shallow) {
      this.track('page_view', { url });
    }
  }
}
