import { IntercomProvider, IntercomProviderProps } from 'react-use-intercom';

type IntercomProps = Omit<React.PropsWithChildren<IntercomProviderProps>, 'appId'> & {
  appId?: string;
};

export default function Intercom(props: IntercomProps) {
  const { appId, children } = props;

  if (appId === undefined) {
    return <>{children}</>;
  }

  return <IntercomProvider {...props} appId={appId} />;
}
